  export default {
    props: {
      visible: {
        type: Boolean,
        default: false,
        required: true,
      },
		title:{
			type: String,
			default: '提示',
		},
      placeholder: {
        type: String,
        default: '请输入密匙',
      },
      mainColor: {
        type: String,
        default: '#e74a39',
      },
      defaultValue: {
        type: String,
        default: '',
      },
      inputStyle: {
        type: String,
        default: '',
      },
      // 是否多行输入的textarea
      isMutipleLine: {
        type: Boolean,
        default: false,
      }
    },
    data() {
      return {
		  // 输入的秘钥值
        value: '',
      }
    },
    watch: {
      visible(val) {
        if(val) {
          this.value = this.defaultValue
        }
      }
    },
    mounted() {
      this.value = this.defaultValue === 'true' ? '' : this.defaultValue
    },
    methods: {
      close() {
        this.$emit('update:visible', false)
      },
      confirm() {
		// console.log(this.value)
        this.$emit('confirm', this.value)
        this.value = ''
      },
    }
  }
